import React, { Component } from 'react';
import {Card,CardImg,Container,Row,Col,Breadcrumb, Modal, Form,Button} from 'react-bootstrap'; 
import {Link} from 'react-router-dom' ;


       
    function RenderDish({dish}) {
        
            return (
                <div className="col-12 col-md-5 mt-5">
                    <Card>
                        <CardImg
                            width={310}
                            height={310} src={dish.image} />
                        <Card.Body>
                            <Card.Title>{dish.name}</Card.Title>
                            <Card.Text>{dish.description}</Card.Text>
                        </Card.Body>
                    </Card>
                </div>
            );
        
    }        
    
    function RenderComments({comments,addComment,dishId}){

        if (comments != null)
        return ( 
            <> 
            <Col md="5">
                <h4>Comments</h4>
                <ul className="List-Unstyled">
                    {comments.map((comment) => {
                        return(
                        <li key={comment.id}>
                            <p>{comment.comment}</p>
                            <p>{comment.author}</p>
                        </li>
                    );

                  })}       
                      
                </ul>
                    <Button onClick={this.toggleModal} color="primary">Submit Comments</Button> 
            <CommentForm dishId={dishId} addComment={addComment} />
            </Col>
        );
            </>
        );
        else
            return(
                <div>NULL Comments</div>
            );    
        }
    

        class CommentForm extends Component{
            constructor(props){
                super(props);
            
            this.toggleModal=this.toggleModal.bind(this)
            this.handleSubmit=this.handleSubmit.bind(this)
            this.state={
                isNavOpen : false,
                isModelOpen :false
            }
            
            }

            toggleModal(){
                this.setState({
                    isModelOpen : !this.state.isModelOpen

                });
            }
            handleSubmit(values){
                this.toggleModal();
                this.props.addComment(this.props.dishId, values.rating, values.author, values.comment);
            }
            render(){
                return (
                    <Modal>
                        <Modal.Header>
                            Submit Comments
                </Modal.Header>
                        <Modal.Body>
                            <Form>
                                <Form.Group htmlFor="rating">
                                    <Form.Label>Example select</Form.Label>
                                    <Form.Control as="select" model=".rating"
                                        ref={(input) => this.rating = input}>
                                        <option>1</option>
                                        <option>2</option>
                                        <option>3</option>
                                        <option>4</option>
                                        <option>5</option>
                                    </Form.Control>
                                </Form.Group>
                                <Form.Group>
                                    <Form.Label htmlFor="name">Your Name</Form.Label>
                                    <Form.Control type="text" id="name" name="name"
                                        ref={(input) => this.name = input}></Form.Control>
                                </Form.Group>
                                <Form.Group htmlfor="comments">
                                    <Form.Label>Example textarea</Form.Label>
                                    <Form.Control id="comments" name="comments" as="textarea" rows="3" />
                                </Form.Group>
                                <Button type="submit" value="submit" color="primary">Submit</Button>
                            </Form>

                        </Modal.Body>
                    </Modal>
                );

                
            }
        }
       
        const DishDetail =(props) => {
            if (props.dish != null)
            return(
                <>
                <Container>
                    <Row>
                        <Breadcrumb>
                            <Breadcrumb.Item><Link to="/home">Home</Link></Breadcrumb.Item>
                            <Breadcrumb.Item><Link to="/menu">Menu</Link></Breadcrumb.Item>
                            <Breadcrumb.Item active>{props.dish.name}</Breadcrumb.Item>
                        </Breadcrumb>
                        <Col>
                            <h3>{props.dish.name}</h3>
                            <hr/>
                        </Col>
                    </Row>
                   

                    
                    <Row>
                        <Col>
                        <RenderDish dish={props.dish} />                  
                        <RenderComments comments={props.comments}
                        addComment= {props.addComment} dishId={props.dish.id} />

                        </Col>
                    </Row>
                   
                    
                </Container>
                </>
                
            );
            else
            return(
                <div>NULL DISH</div>
            );

  
    }

 
export default DishDetail;

