import React, { Component } from 'react';
import {Navbar,NavbarBrand,Nav,NavbarToggler,Collapse,NavItem,Jumbotron, Container,
    NavDropdown,Form,FormControl,Button,Row,Col,Modal } from 'react-bootstrap';
import {NavLink} from 'react-router-dom';


class Header extends Component {
    constructor(props){
        super(props);
        this.state={
            isNavOpen : false,
            isModelOpen : false
        }
        this.toggleNav=this.toggleNav.bind(this)
        this.toggleModal= this.toggleModal.bind(this)
        this.handleLogin = this.handleLogin.bind(this);
    }
    handleLogin(event) {
        this.toggleModal();
        console.log("Username: " + this.username.value + " Password: " + this.password.value
            + " Remember: " + this.remember.checked);
        alert("Username: " + this.username.value + " Password: " + this.password.value
            + " Remember: " + this.remember.checked);
        event.preventDefault();

    }
    toggleNav(){
        this.setState (
            {
                
                
                isNavOpen : false,
                isModelOpen : false,
            }
        );
    }
    toggleModal() {
        this.setState({
            isModalOpen: !this.state.isModalOpen
        });
    } 
    
    render() { 
        return (  
            
            <>
                <Navbar collapseOnSelect bg="primary" expand="md" variant="dark">
                   
                    <Navbar.Brand href="#home">
                        <img
                            alt=""
                            src="/assets/images/logo.png"
                            width="41"
                            height="35"
                            className="d-inline-block align-top"
                        />{' '} EE THATTUKADA
                    </Navbar.Brand>

                    <Navbar.Toggle aria-controls="responsive-navbar-nav" onClick={this.toggleNav}/>
                    <Navbar.Collapse id="responsive-navbar-nav" show={this.state.isNavOpen}>

                    <Nav className="mr-auto">
                        <Nav.Link href="/home"><span className="fa fa-home fa-lg"></span> Home
                        </Nav.Link>
                        <Nav.Link href="/aboutus"><span className="fa fa-info fa-lg"></span> About Us
                        </Nav.Link>
                            <Nav.Link href="/menu"><span className="fa fa-list fa-lg"></span> Menu</Nav.Link>
                        <Nav.Link href="contactus"><span className="fa fa-address-card fa-lg"></span> Contact Us</Nav.Link>
                    
                        <NavDropdown title="Dropdown" id="collasible-nav-dropdown">
                            <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                            <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                            <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                        </NavDropdown>
                    
                    </Nav>
                    </Navbar.Collapse> 
                   

                    <Nav className="ml-auto" navbar>
                        <Nav.Item>
                            <Button outline onClick={this.toggleModal}><span className="fa fa-sign-in fa-lg"></span> Login</Button>
                        </Nav.Item>
                    </Nav>
                    <Form inline>
                        <FormControl type="text" placeholder="Search" className="mr-sm-2" />
                        <Button variant="outline-light">Search</Button>
                    </Form>
                    
                </Navbar>
                <Jumbotron>
                    <Container>
                        <Row>
                            <Col xs md="5"><h1>EE THATTUKADA</h1></Col>
                            
                        </Row>
                        <Row>
                            <Col xs md="5"><p>THE HOUSE OF COMMONS IN CHANGANACHERRY</p></Col>
                        </Row>
                    </Container>
                </Jumbotron>
               
                <Modal show={this.state.isModalOpen} toggle={this.toggleModal}>
                    <Modal.Header toggle={this.toggleModal} closeButton >
                        Login Here
                </Modal.Header>
                    <Modal.Body>

                        <Form onSubmit={this.handleLogin}>
                            <Form.Group>
                                <Form.Label htmlFor="username">Username</Form.Label>
                                <Form.Control type="text" id="username" name="username"
                                    ref={(input) => this.username = input} />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label htmlFor="password">Password</Form.Label>
                                <Form.Control type="password" id="password" name="password"
                                    ref={(input) => this.password = input} />
                            </Form.Group>
                            <Form.Group check>
                                <Form.Label check>
                                    <Form.Control type="checkbox" name="remember"
                                        ref={(input) => this.remember = input} />
                                    Remember me
                                </Form.Label>
                            </Form.Group>
                            <Button type="submit" value="submit" color="primary">Login</Button>
                        </Form>

                    </Modal.Body>
                    
                </Modal>
                




            </>  
            
        );
    }
}
 
export default Header;