import React, { Component } from 'react';
import {GoogleApiWrapper,MapContainer} from './MapComponent';
import {Card,Breadcrumb,Button,ButtonGroup,Form,FormControl,InputGroup,Container,Row,Col} from 'react-bootstrap';


class Contact extends Component {
    constructor(props) {
        super(props);

        this.state = {
            firstname: '',
            lastname: '',
            telnum: '',
            email: '',
            agree: false,
            contactType: 'Tel.',
            message: '',
            touched: {
                firstname: false,
                lastname: false,
                telnum: false,
                email: false
            }
        }
        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleBlur = this.handleBlur.bind(this);

    }
    handleInputChange(event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState({
            [name]: value
        });
    }

    handleSubmit(event) {
        console.log('Current State is: ' + JSON.stringify(this.state));
        alert('Current State is: ' + JSON.stringify(this.state));
        event.preventDefault();
    }
    handleBlur = (field) => (evt) => {
        this.setState({
            touched: { ...this.state.touched, [field]: true }
        });
    }

    validate(firstname, lastname, telnum, email) {
        const errors = {
            firstname: '',
            lastname: '',
            telnum: '',
            email: ''
        };

        if (this.state.touched.firstname && firstname.length < 3)
            errors.firstname = 'First Name should be >= 3 characters';
        else if (this.state.touched.firstname && firstname.length > 10)
            errors.firstname = 'First Name should be <= 10 characters';

        if (this.state.touched.lastname && lastname.length < 3)
            errors.lastname = 'Last Name should be >= 3 characters';
        else if (this.state.touched.lastname && lastname.length > 10)
            errors.lastname = 'Last Name should be <= 10 characters';

        const reg = /^\d+$/;
        if (this.state.touched.telnum && !reg.test(telnum))
            errors.telnum = 'Tel. Number should contain only numbers';

        if (this.state.touched.email && email.split('').filter(x => x === '@').length !== 1)
            errors.email = 'Email should contain a @';

        return errors;
    }


        
    render(){
        const errors = this.validate(this.state.firstname, this.state.lastname, this.state.telnum, this.state.email);
    return (
        
        <Container>
            <Row>
                <Col md="6" className="mt-5">
                    <h3>Location Information</h3>
                </Col>
                <Col md="7">
                    <h5>Our Address</h5>
                    <address>
                        Thattukada Bldg.<br />
                        Near Revenue Tower, T.B. Road<br />
                        Changanacherry,Kerala.<br />
                        <i className="fa fa-phone"></i>:9645916911<br />
                        <i className="fa fa-fax"></i>:9645916911<br />
                        <i className="fa fa-envelope"></i>: <a href="mailto:mannaconsultents@gmail.com">mannaconsultents@gmail.com</a>
                    </address>
                </Col>
                <Card>
                   <h1>LOCATION MAP HERE</h1>
                   
                </Card>
                <Col className="mt-3">
                    <ButtonGroup>
                        <a role="button" className="btn btn-primary" href="tel:+91 9645916911"><i className="fa fa-phone"></i> Call</a>
                        <a role="button" className="btn btn-info"><i className="fa fa-skype"></i> Skype</a>
                        <a role="button" className="btn btn-success" href="mailto:mannaconsultents@gmail.com"><i className="fa fa-envelope-o"></i> Email</a>
                    </ButtonGroup>
                </Col>
            </Row>
            <Row>
                <Col className="col-md-9 mt-4">
                <h3>Please send us your Feedback</h3>
                </Col>
                <Col className ="col-md-6 mt-5">
                    <Form onSubmit={this.handleSubmit}>
                       
                            <Form.Group as={Row} controlId="firstname">                      
                                
                            <Form.Label column sm="4" htmlFor="firstname" >First Name</Form.Label>
                                <Col sm={8}>
                                <Form.Control type="text" name="firstname" placeholder="First Name" 
                                    value={this.state.firstname}
                                    valid={errors.firstname === ''}
                                    invalid={errors.firstname !== ''}
                                    onBlur={this.handleBlur('firstname')}
                                    onChange={this.handleInputChange} />
                                <FormControl.Feedback>{errors.firstname}</FormControl.Feedback>   
                                </Col>                             
                            </Form.Group>                                                         
                               
                        <Form.Group as={Row} controlId="lastname">
                            <Form.Label column sm="4" htmlFor="lastname">Last Name</Form.Label>
                            <Col sm={8}>
                            <Form.Control type="text" name="lastname" placeholder="Last Name" 
                                    value={this.state.lastname}
                                    valid={errors.lastname === ''}
                                    invalid={errors.lastname !== ''}
                                    onBlur={this.handleBlur('lastname')}
                                    onChange={this.handleInputChange}/>
                                <FormControl.Feedback>{errors.lastname}</FormControl.Feedback>
                            </Col>                           

                        </Form.Group>      
                            
                        <Form.Group as = {Row}controlId="telnum">
                                <Form.Label column sm="4" htmlFor="telnum">Tel.Number</Form.Label>
                                <Col sm={8}>
                                    <Form.Control type="tel" name="telnum" placeholder="Tel.Number" 
                                    value={this.state.telnum}
                                    valid={errors.telnum === ''}
                                    invalid={errors.telnum !== ''}
                                    onBlur={this.handleBlur('telnum')}
                                    onChange={this.handleInputChange}/>
                                <FormControl.Feedback>{errors.telnum}</FormControl.Feedback>
                                </Col>                               
                            
                        </Form.Group>
                            <Form.Group as={Row} controlId="email">
                                <Form.Label column sm="4" htmlFor="email">Email</Form.Label>
                                <Col sm={8}>
                                    <Form.Control type="email" name="email" placeholder="Email" 
                                    value={this.state.email}
                                    valid={errors.email === ''}
                                    invalid={errors.email !== ''}
                                    onBlur={this.handleBlur('email')}
                                    onChange={this.handleInputChange}/>
                                <FormControl.Feedback>{errors.email}</FormControl.Feedback>
                                     </Col>
                            </Form.Group>



                        <Form.Row className="align-items-center" >

                                <Col xs="auto" className="my-1">
                                    <Form.Check
                                        type="checkbox"
                                        name="agree"
                                        checked={this.state.agree}
                                        onChange={this.handleInputChange}
                                        id="customControlAutosizing"
                                        label="Can we Contact you,please ?"
                                        custom />
                                </Col>
                            <Col xs="auto" className="my-1">
                                    <Form.Label column sm="4" htmlFor="contactType">
                                
                                </Form.Label>
                                    <Form.Control 
                                        as="select"
                                        className="mr-sm-2"
                                        id="contactType"
                                        value={this.state.contactType}
                                        onChange={this.handleInputChange}
                                        
                                        custom >

                                        <option value="0" >Tel.</option>
                                        <option value="1" >Email</option>

                                    </Form.Control>
                                </Col>
                            </Form.Row>

                           
                                <Form.Group as={Row}>
                                    <Form.Label column md="4" htmlFor="message">Your Feedack</Form.Label>
                                    <Col sm={8}>
                                        <Form.Control as="textarea" controlId="message" name="message" rows="9"
                                        value={this.state.message}
                                        onChange={this.handleInputChange}>

                                        </Form.Control>
                                    </Col>
                                </Form.Group>
                            
                            <Button size="lg" block type="submit" variant="primary">Send Feedback</Button>

                        </Form>
                    </Col>
                </Row>
            </Container>

        );
    }
}
export default Contact;                                        
                    
                        
                            

                            
                        
                    
                        

                                  
                            
                        
                        
                    
       
        
     
    
  

    
    